.modal{
  background-color: rgb(213, 234, 252);
  padding: 20px;
  margin-bottom: 20px;
}

.list {
  background-color: rgb(255, 252, 240);
  padding: 20px;
  border-bottom: 1px solid gray;
  display: flex;
  position: relative;
}

.list span{
  margin-left: 8px;
}

.list button{
  width: 80px;
  height: 30px;
  position: absolute;
  right: 20px;
  top: 50px;
}